@import url(https://fonts.googleapis.com/css2?family=Hind:wght@300;400;500;600;700&display=swap);
.container{
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
    min-height: 100vh;
    min-width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    background-color: #D3E0EA;
    color: #0A043C;
    font-family: 'Hind', sans-serif;
}

.content {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav{
    width: 100%;
    height: 60px;
    padding-right: 25px;
    padding-left: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    /* border-bottom: 3px solid #03506F; */
}

.nav-cont {
    width: auto;
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.nav-cont * {
    display: flex;
    flex-direction: row;
    font-size: 18px;
    color: #0A043C;
}

.nav-item:hover {
    color: #4f4797;
}

.firstname {
    text-transform: capitalize;
}

.lastname {
    color: #6B7AA1;
    font-weight: 500;
}

.lastname {
    margin-right: 20px;
}

.admin-tag {
    width: 15px;
    height: 15px;
    border-radius: 5px;
    background: #6C5B7B;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 8pt;
}

.userID {
    margin-right: 20px;
    font-size: 14pt;
}

.highlight {
    color: #C1A1D3;
    margin-left: 10px;
    margin-right: 10px;
}

.page {
    font-weight: 600;
    margin-top: 3em;
    font-size: 40px;
}

.page h4 {
    text-align: center;
    color: #276678;
}

.logo {
    font-weight: 700;
    font-size: 24px;
}

.nav-item {
    margin-left: 4em;
}

h1 {
    font-size: 30px;
    font-weight: 500;
    margin-top: 3em;
}

input {
    background-color: #213050;
    padding: 10px;
    border-radius: 1em;
    padding-left: 15px;
    border: none;
    color: #DCD6F7;
}

select {
    background-color: #213050;
    padding: 8px;
    border-radius: 0.8em;
    color: #DCD6F7;
    border: none;
    margin-left: 12px;
}

input::-webkit-input-placeholder {
    color: #DCD6F7;
}

input:-ms-input-placeholder {
    color: #DCD6F7;
}

input::-ms-input-placeholder {
    color: #DCD6F7;
}

input::placeholder {
    color: #DCD6F7;
}

input:focus, select:focus {
    outline:none;
    border: none;
}

.password {
    margin-left: 20px;
}

.submit {
    background-color: #F4EEFF;
    color: #424874;
    font-weight: 600;
    padding: 10px;
    margin-left: 20px;
    padding-left: 15px;
    padding-right: 15px;
}

.register {
    margin: 1.3em;
    margin-left: 1.5em;
}

h2 {
    font-size: 25px;
}

.register-input {
    margin-left: 12px;
}
.dash {
    height: 90vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

.dash-item {
    height: 100%;
    display: flex;
}

.sidemenu {
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0px;
    border-right: 1px groove #6C5B7B;
    padding-top: 0;
}

.problems-header {
    width:  100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #8896b8;
    font-weight: 10px;
    background: #012b36;
    margin-bottom: 20px;
}

.problem {
    width: 85%;
    height: 40px;
    margin-bottom: 8px;
    border-radius: 2px;
}

.dash-content {
    width: 100%;
    height: 100%;
    margin-left: 10px;
    padding: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.dash-content-item {
    height: 100%;
}

.editor-container{
    width: 70%;
}

.editor {
    width: 100%;
}

.right-side {
    width: 29.5%;
}

.console {
    width: 100%;
    height: 40%;
    margin-bottom: 10px;
}

.console-editor {
    color: #2C2E43;
}

.problem-info {
    border: 1px solid #6C5B7B;
    border-radius: 2px;
    padding: 10px;
    margin-bottom: 10px;
    width: 100%;
    height: 35%;
}

.title {
    font-size: 18pt;
    font-weight: 600;
    color: #0A043C;
    margin-left: 5px;
    margin-bottom: 5px;
}

.problem-divider {
    color: #0A043C;
}

.description {
    font-weight: 400;
    color: #213050;
    margin-left: 5px;
}

.problem-status {
    margin-bottom: 10px;
    width: 99%;
    height: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding-left: 10px;
}

.status-item {
    margin-right: 20px;
    display: flex;
    width: auto;
    font-size: 12pt;  
}
.dropdown-problems {
    margin-top: 10px;
    margin-bottom: 10px;
    width: 90%;
}

.dropdown-problems-item {
    width: 100%;
}

.spacing {
    margin-right: 10px;
}

.sidemenu {
    overflow-y:auto;
    overflow-x:hidden;
}

.dropdown-problems-user {
    margin-top: 0px;
}

.dropdown-problems-item-user {
    margin-top: 0px;
    margin-bottom: 0px;
    font-size: 10pt;

    background: white;
    color: #133B5C;
    border: solid 1px #133B5C;
    border-radius: 10px;

    overflow: hidden;
    margin-right: 100px;
}

.drop-item {
    color: #133B5C; 
    font-size: 10pt;
}

.dropdown-menu {
    max-height: 500px;
    overflow-y: auto;
    overflow-x: hidden;
}

